import React from "react";
import { Select } from "antd"
import { AccessControlTypes } from "types";

export default function AccessControlTypeSelector(props) {
    return (
        <Select
            {...props}
            mode="multiple"
            style={{ width: "100%" }}
        >
            <Select.Option value={AccessControlTypes.Create}>Create</Select.Option>
            <Select.Option value={AccessControlTypes.Edit}>Edit</Select.Option>
            <Select.Option value={AccessControlTypes.Delete}>Delete</Select.Option>
            <Select.Option value={AccessControlTypes.View}>View</Select.Option>
            <Select.Option value={AccessControlTypes.Execute}>Execute</Select.Option>
        </Select>
    );
}
